<template>
    <section class="bg-white text-white pt-4">
         <!-- <figure>
            <img src="@/assets/img/singlepole-top-blue.png" alt="" />
         </figure> -->

         <div class="bg-blue pb-12">
            <h2 class="text-3xl py-6 text-white 
            md:text-6xl
            lg:text-4xl">- Crear nueva cuenta -</h2>            

            <div class="container border-dashed border-2 rounded border-white p-5 m-auto">
              <form class="flex gap-x-3 gap-y-6 flex-wrap shadow-round bg-darkBlue text-xl px-10 py-8"
              @submit="submitForm">        
                <h3 class="w-full text-left border-b font-bold text-lg md:text-2xl pb-2">Informacion Personal</h3>
                
                <!-- Informacion Personal -->
                <div class="flex md:flex-wrap gap-y-6 gap-x-3 w-full flex-col md:flex-row">
                  <input name="first_name" 
                    type="text" 
                    placeholder="Nombre *"
                    required
                    v-model="formData.first_name" 
                    class="w-full md:w-5/12 border rounded-4xl text-orange bg-darkBlue text-left px-6 py-2 text-base md:text-xl">                 

                  <input name="email" 
                    type="email" 
                    placeholder="E-Mail *" 
                    required
                    v-model="formData.email" 
                    class="w-full md:w-4/12 border rounded-4xl text-orange bg-darkBlue text-left px-6 py-2 text-base md:text-xl">

                  <input name="last_name" 
                    type="text" 
                    placeholder="Apellido *"
                    required
                    v-model="formData.last_name" 
                    class="w-full md:w-5/12 border rounded-4xl text-orange bg-darkBlue text-left px-6 py-2 text-base md:text-xl">                                     

                  <input name="phone" 
                    type="text" 
                    placeholder="Teléfono" 
                    v-model="formData.phone" 
                    class="w-full md:w-4/12 border rounded-4xl text-orange bg-darkBlue text-left px-6 py-2 text-base md:text-xl">

                    <div class="flex flex-col items-start w-full">       
                      <h4 class="w-full text-left pb-3">Genero *</h4>            
                      <div class="flex border rounded-4xl items-start gap-x-6 p-3">
                        <label>
                          <input name="gender" type="radio" value="1" v-model="formData.gender_id">
                          <span class="text-base md:text-xl px-1 md:px-2">Masculino</span>
                        </label>                    
                        <label>
                          <input name="gender" type="radio" value="2" v-model="formData.gender_id">                
                          <span class="text-base md:text-xl px-1 md:px-2">Femenino</span>
                        </label>
                      </div>
                  </div>
                </div>

                <h3 class="w-full text-left border-b font-bold text-lg md:text-2xl pb-2">Seguridad</h3>
                <!-- Seguridad -->
                <div class="flex flex-col items-center md:flex-row gap-x-3 gap-y-6 md:gap-y-0 w-full md:w-auto text-left font-bold text-2xl pb-2">
                  <input name="password" 
                    :type="(showpass) ? 'text' : 'password'" 
                    placeholder="Contraseña *"
                    required
                    v-model="pass" 
                    class="w-3/10 border rounded-4xl text-orange bg-transparent text-left px-6 py-2">    

                  
                    <input name="repassword" 
                      :type="(showpass) ? 'text' : 'password'" 
                      placeholder="Repetir Contraseña *"
                      required
                      v-model="repassword"                     
                      class="w-full md:w-3/10 border rounded-4xl text-orange bg-darkBlue text-left px-6 py-2 text-base md:text-xl">                      
                    <div class="flex items-center gap-x-4">                    
                      <button type="button" @click="showpass=!showpass" v-show="!showpass"><i class="fas fa-eye-slash"></i></button>
                      <button type="button" @click="showpass=!showpass" v-show="showpass"><i class="fas fa-eye"></i></button>
                      <small class="text-red-700" v-show="!samepass">Las contraseñas deben ser iguales.</small>
                    <!-- <div class="flex flex-nowrap gap-6 "></div> -->
                  </div>
                </div>

                <hr>
                <h3 class="w-full text-left border-b font-bold text-lg md:text-2xl pb-2">Direcciones</h3>
                <!-- Direcciones -->
                <div class="flex flex-wrap gap-y-6 gap-x-3 w-full">
                  <input name="address" 
                    type="text" 
                    placeholder="Dirección" 
                    v-model="formData.address" 
                    class="w-full md:w-8/12 border rounded-4xl text-orange bg-darkBlue text-left px-6 py-2 text-base md:text-xl">
                    
                    <Multiselect
                        v-model="country" 
                        mode="single"
                        :searchable="true"                                               
                        :options="countries"
                        placeholder="Seleccione País *"     
                        :classes="select_style_opt"
                      />
                  <!-- <model-select :options="countries"
                        v-model="country"
                        placeholder="Seleccione País *"
                        required
                        class="border border-white rounded-4xl text-orange bg-darkBlue font-500 text-xl text-left px-6 py-3"
                        >
                  </model-select> -->

                  <input name="province" 
                    type="text" 
                    placeholder="Provincia" 
                    v-model="formData.province" 
                    class="w-full md:w-4/12 border rounded-4xl text-orange bg-darkBlue text-left px-6 py-2 text-base md:text-xl">

                  <input name="city" 
                    type="text" 
                    placeholder="Ciudad" 
                    v-model="formData.city" 
                    class="w-full md:w-4/12 border rounded-4xl text-orange bg-darkBlue text-left px-6 py-2 text-base md:text-xl">
                  
                  <input name="zip_code" 
                    type="text" 
                    placeholder="Codigo Postal" 
                    v-model="formData.zip_code" 
                    class="w-full md:w-4/12 border rounded-4xl text-orange bg-darkBlue text-left px-6 py-2 text-base md:text-xl">                   
                </div>

                <hr>                
                <h3 class="w-full text-left border-b font-bold text-lg md:text-2xl pb-2">Informacion de Agencia</h3>
                <input id="is_agent" name="is_agent" type="checkbox" v-model="formData.is_agent">
                <label for="is_agent">Agente de viaje</label>
                <!-- Informacion de Agencia -->
                <div class="flex gap-3 w-full">
                  <input name="company" 
                    type="text" 
                    placeholder="Compañía" 
                    v-model="formData.company" 
                    class="w-full md:w-2/7 border rounded-4xl text-orange bg-darkBlue text-left px-6 py-2 text-base md:text-xl">

                  <input name="iata" 
                    type="text" 
                    placeholder="IATA" 
                    v-model="formData.iata" 
                    class="w-full md:w-2/7 border rounded-4xl text-orange bg-darkBlue text-left px-6 py-2 text-base md:text-xl">
                </div>

                <div class="flex flex-col text-left w-full py-6">
                  <small>
                    Los campos obligatorios marcados con un (*) deben ser rellenados.
                  </small>
                  <div v-if="error_message" class="error-message my-3 items-center gap-3 px-4">
                    <span class="text-4xl"><i class="fa fa-times-circle"></i></span> 
                    <h5 class="text-xl self-center px-2">
                      Error enviando informacion. No se pudo crear el nuevo usuario
                      <p>{{error_message_text}}</p>
                    </h5>
                  </div>
                  <div v-if="success_message" class="success-message my-3 items-center gap-3 px-4">
                    <span class="text-4xl"><i class="fa fa-check-circle"></i></span> 
                    <div>
                      <h5 class="text-xl self-center">Se creó el usuario exitosamente!!!.</h5>
                      <p v-if="formData.is_agent" class="text-lg">Se ha enviado un correo a la direccion {{ formData.email }} para que el usuario verifique su cuenta.</p>
                    </div>
                  </div>
                </div>
                <!-- <div class="w-full flex gap-3 pt-10" @click="activeTerm = !activeTerm">  
                  <span class="cursor-pointer border rounded-4xl text-orange bg-darkBlue px-2 py-1"
                  :class="(activeTerm) ? 'text-orange' : 'text-darkBlue'">
                    <i class="fas fa-check"></i>
                  </span>
                  <label class="self-center" for="term">Term and condition</label>
                </div> -->

                <button type="submit" 
                  class="w-full md:w-auto uppercase bg-orange text-white border border-orange text-xl rounded-4xl font-bold ml-auto px-12 py-1 hover:bg-darkBlue hover:text-orange">
                  Enviar
                </button>
              </form>
            </div>
         </div>
         
    </section>
</template>
<script>
import Multiselect from '@vueform/multiselect'
import '@vueform/multiselect/themes/default.css'

import Authorization from "@/services/user/auth.service";
import ClientService from "@/services/user/client.service";

export default {
    name:'NewClient',
    components: {
      Multiselect
    },
    data() {
      return {
        error_message_text: '',
        error_message: false,
        success_message: false,
        activeTerm: false,    
        samepass: true,            
        showpass: false,     
        pass: '',               
        repassword: '',        
        countries: [],
        select_style_opt: {
              container: 'relative w-full md:w-4/12 flex text-orange items-center justify-end box-border cursor-pointer border border-white rounded-3xl bg-transparent text-xl leading-snug outline-none',
              search: 'w-full md:w-4/12 absolute inset-0 outline-none focus:ring-0 appearance-none box-border border-8 border-transparent text-base font-sans bg-transparent rounded-3xl pl-2 rtl:pl-0 rtl:pr-3.5',
              options: 'flex flex-col p-0 m-0 list-none text-blue',
              optionSelected: 'text-white bg-lightGray'
          },
        country: {
          value: '',
          label: ''
        },
        formData: {},
      }
    },    
    created() {
      this.getCountry()
      this.clearForm()
    },   
    watch: {
      repassword() {        
        this.samepass = (this.repassword == this.pass || (this.repassword == '' && this.pass == ''))
      }      
    },    
    methods: {
      async getCountry() {
        try {
          const {status, data} = await Authorization.checkLogin('/country')          
          if (status == 200) {
            for(let country of data) {
              this.countries.push({
                value: country.id,
                label: country.alias
              })
            }
            console.log(this.countries)
          }
        } catch(e) {
          console.log(e.response);
        }
      },
      async submitForm(e) {
        e.preventDefault();
        // VALIDATE FORM
        if(this.validate(this.formData.first_name) || this.validate(this.formData.last_name) || this.validate(this.formData.gender_id) ||
          this.validate(this.formData.email) || this.validate(this.country))
        { 
          this.formData.password = this.pass
          this.formData.country_id = this.country          
          ClientService.addUpdClient(this.formData, true).then(res => {                     
            if(res.status == 'OK') {                
                this.error_message = false
                this.success_message = true                
                setTimeout(() => {
                    this.success_message = false                    
                    this.clearForm()
                }, 8000); 
            } else {
              this.error_message = true
              this.success_message = false
              setTimeout(() => {
                  this.error_message = false
              }, 8000); 
              this.error_message_text = res.data//'El email del usuario que desea crear ya esta existe.'
            }                  
          })
        } else {
            this.error_message = true
            this.success_message = false
            setTimeout(() => {
                this.error_message = false
            }, 8000); 
            this.error_message_text = 'Faltan datos por llenar'
        } 
      },
      validate(value) {
          if (value == '')
              return false
          else if(value === undefined)
              return false
          else if(value === null)
              return false
          return true
      },  
      clearForm() {
        this.formData = {
            //general info
                first_name: '',
                last_name: '',
                phone: '',
                email: '',
                parent: '',
                parent_id: '',
                gender_id: '',                
                password: '',                
            //location
                address: '',
                city: '',
                province: '',
                zip_code: '',
            //agency
                iata: '',
                company: '',
                is_agent: '',                
                country_id: '',                
                roles: [],
                margin: []
        }
        this.pass = ''
      }  
    }      
}
</script>
<style scoped>  
  input[name="password"] {
    -webkit-text-security:disc
  }
</style>